    methods:{
        handleSubmit(){
            //向服务器发送详细表单数据
            if(this.currentOption === '应用层')
                this.currentSub = '应用层';
                var i = 0;
                for(i = 0;i<4;i++){
                    this.answer[i]=0;
                }
            if(this.questionType == 0){
                switch(this.answerForSingle){
                    case 'A':{
                        this.answer[0] = 1;
