                    this.answer[2] = 1;
                if(this.answerForMultiple.find(item =>{item === 'D'}))
                    this.answer[3] = 1;
            }
            this.answernum = this.answer[3]+this.answer[2]*2+this.answer[1]*4+this.answer[0]*8;

            axios.post('/#',{
                id:0,
                mainTitle:this.currentOption,
                subTitle:this.currentSub,
                content:this.question,
                
                    A:this.A,
                    B:this.B,
                    C:this.C,
                    D:this.D,
               
                questionType:this.questionType,//单选为0，多选为1
                answer:this.answernum,//为一个十进制数
            })
            .then(function(response){
                if(response.data.operateResult == false){
                    console.log("操作失败");
                }
                else if(response.data.operateResult == true){
                    console.log("操作成功");
                }
                else if(response.data.operateResult == ''){
                    console.log("重复值");
                }
                else{
                    console.log("数据传输出错");
                }
            })
            .catch(function(error){
                console.log(error);
            });
